                      <h6>No Videos</h6>
                    </div>
                  </b-card-text>
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
          <div class="accordion" role="tablist">
            <b-card no-body class="mb-1">
              <b-card-header header-tag="header" class="p-1" role="tab">
                <b-button class="w-100 catsy fw-bold d-flex justify-content-between btn-sm"
                          v-b-toggle.accordion-6 variant="light">Contact</b-button>
              </b-card-header>
              <b-collapse id="accordion-6" visible accordion="my-accordion" role="tabpanel">
                <b-card-body>
                  <b-card-text>
                    <h2 class="fw-bold">{{bprofile.name}}</h2>
                    <p class="fw-bold">
                      <v-icon>mdi-map-marker</v-icon>
                      {{ bprofile.city }}, {{bprofile.state}}
                    </p>
                    <a :href="('tel:'+ bprofile.mobile_phone)" class="text-decoration-none">
                      <v-btn color="bgred" class="me-1 my-1 text-light">
                        <v-icon left> mdi-phone </v-icon>
                       {{bprofile.mobile_phone}}
                      </v-btn>
                    </a>
                    <a :href="('https://wa.me/'+bprofile.mobile_phone+'?text=rf')" target="_blank" class="text-decoration-none">
                      <v-btn color="success" class="me-1 my-1">
                        <v-icon left> mdi-whatsapp </v-icon>
                        Whatsapp
                      </v-btn>
                    </a>
                  </b-card-text>
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
          <div class="accordion" role="tablist" id="accordionReview">
            <b-card no-body class="mb-1">
              <b-card-header header-tag="header" class="p-1" role="tab">
                <b-button class="w-100 catsy fw-bold d-flex justify-content-between btn-sm"
                          v-b-toggle.accordion-7 variant="light">Reviews</b-button>
              </b-card-header>
              <b-collapse id="accordion-7" visible accordion="my-accordion" role="tabpanel">
                <b-card-body>
                  <b-card-text>
                    <div class="text-center fw-bold display-3">{{ bprofile.allreview_avg_rating }}</div>
                    <div class="ratings text-center">
                      <v-rating :value="bprofile.allreview_avg_rating" color="primary" hover  half-increments size="24" readonly></v-rating>
                      <div class="small text-center">Based on {{ bprofile.allreview_count }} Reviews</div>
                      <div v-if="!loggedUser">
                        <router-link to="/user/login">Write a Review</router-link>
                      </div>
                      <div v-else>
                        <a @click="openReviewModal" class="mt-5">Write a Review</a>
                      </div>
                    </div>
                    <div class="list-unstyled">
                      <div class="row align-items-center">
                        <div class="col-4 col-md-2 py-2">Excellent:</div>
                        <div class="col-8 col-md-10 py-2">
                          <b-progress height="6px" :value="(count5*100/bprofile.allreview_count)" variant="success" :key="count5"></b-progress>
                        </div>
                      </div>
                      <div class="row align-items-center">
                        <div class="col-4 col-md-2 py-2">Good:</div>
                        <div class="col-8 col-md-10 py-2">
                          <b-progress height="6px" :value="(count4*100/bprofile.allreview_count)" variant="secondary" :key="count4"></b-progress>
                        </div>
                      </div>
                      <div class="row align-items-center">
                        <div class="col-4 col-md-2 py-2">Average:</div>
                        <div class="col-8 col-md-10 py-2">
                          <b-progress height="6px" :value="(count3*100/bprofile.allreview_count)" variant="info" :key="count3"></b-progress>
                        </div>
                      </div>
                      <div class="row align-items-center">
                        <div class="col-4 col-md-2 py-2">Below Average:</div>
                        <div class="col-8 col-md-10 py-2">
                          <b-progress height="6px" :value="(count2*100/bprofile.allreview_count)" variant="warning" :key="count2"></b-progress>
                        </div>
                      </div>
                      <div class="row align-items-center">
                        <div class="col-4 col-md-2 py-2">Poor:</div>
                        <div class="col-8 col-md-10 py-2">
                          <b-progress height="6px" :value="(count1*100/bprofile.allreview_count)" variant="danger" :key="count1"></b-progress>
                        </div>
                      </div>
                    </div>
                  </b-card-text>
                  <div v-if=" bprofile.allreview.length > 0">
                    <div class="row">
                      <div class="col-12 col-md-6" v-for="(review,i) in bprofile.allreview" :key="i">
                        <v-card>
                          <v-card-text>
                            <v-row>
                              <v-col cols="2" md="2">
                                <div v-if="review.user_avatar === null">
                                  <v-avatar color="indigo" size="36">
                                    <span class="white--text text-h5">!</span>
                                  </v-avatar>
                                </div>
                                <div v-else>
                                  <v-avatar>
                                    <img :src="(url+review.user_avatar)" alt="avatar">
                                  </v-avatar>
                                </div>
                              </v-col>
                              <v-col cols="7" md="7">
                        <span class="h5 link-dark ms-1">
                            {{ review.name }}
                            <v-rating :value="review.rating" color="amber" dense readonly half-increments size="20">
                            </v-rating>
                       </span>
                              </v-col>
                              <v-col cols="3" md="3"><span class="small text-muted">1 Day ago</span></v-col>
                            </v-row>
                            <v-row>
                              <v-col cols="12" md="12"><p class="link-dark mb-0">{{ review.review_text }}</p></v-col>
                            </v-row>
                          </v-card-text>
                        </v-card>
                      </div>
                    </div>
                  </div>
                <div v-else>
                  <div class="row">
                    <div class="col-12 col-md-12 text-center">
                      <h5>No Reviews</h5>
                    </div>
                  </div>
                </div>
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
        </v-col>
      </v-row>
    </v-container>
    <div v-if="!loggedUser">
      <!-- <router-link to="/user/login">Write a Review</router-link> -->
    </div>
    <div v-else>
      <v-dialog v-model="showModel1" persistent max-width="900">
      <v-card>
        <v-form v-on:submit.prevent="reviewForm" ref="ReviewForm" id="reviewFrm" v-model="valid" lazy-validation>
            <v-card-title class="text-h5 grey lighten-2">{{ bprofile.name }}</v-card-title>
            <v-card-text>
              <div class="row">
                <div class="col-12">
                  <v-btn elevation="2" rounded color="cred" >
                     <v-icon left>mdi-arrow-left</v-icon>
                      Back to {{ bprofile.name }}
                  </v-btn>
                </div>
                <div class="col-12">
                  <h4>User-{{ loggedUser.name }}</h4>
                </div>
              </div>
             
                <v-rating v-model="rating" color="yellow darken-3" background-color="grey darken-1"           empty-icon="$ratingFull" half-increments hover large>
                </v-rating>
                <v-textarea 
                          v-model="review"
                          :rules="ReviewRules"
                          :counter="1000"
                          rows="3" 
                          row-height="20" 
                          label="Please Write Your Review Here">
                </v-textarea>
            </v-card-text>
            <div class="text-end">
              <v-btn class="my-2 me-3 cred" type="submit" :disabled="!valid">SAVE</v-btn>
              <v-btn @click="showModel1 = false" class="my-2 me-3 cred">CLOSE</v-btn>
            </div>
        </v-form>
      </v-card>
    </v-dialog>
    </div>
    <b-modal id="model-login" title="Login" hide-footer>
      <form ref="form" @submit.stop.prevent="">
        <div class="mb-2 login-imge">
          <img src="https://bizlx.itechvision.in/images/login-img.png" class="login-image">
        </div>
        <div class="py-2">
          <b-form-input type="email" id="email" v-model="name" required placeholder="Enter email"></b-form-input>
        </div>
        <div class="py-2">
          <b-form-input type="password" id="password" v-model="name" required placeholder="password"></b-form-input>
        </div>
        <div class="mt-3 singup-button">
          <router-link to="/businessprofile">
            <b-button variant="success" type="submit">Submit</b-button>
          </router-link>
          <p> Don't have an account? 
            <router-link to="/user/register" class="cred">Sign up</router-link>
          </p>
        </div>
      </form>
    </b-modal>
    <div v-if="!loggedUser">
        <v-btn  class="enq" color="cred"><router-link to="/user/login" class="text-white">Enquiry</router-link></v-btn>
        </div>
        <div v-else v-b-modal.model-enq>
        <v-btn  class="enq" color="cred">Enquiry</v-btn>
        </div>
    <div class="enq-close">
      <b-modal id="model-enq" title="Send Enquiry" class="fw-bold" hide-footer >
       <form action="#" id="frm" ref="anyName">
        <div class="row">
            <div class="col-12 col-md-6 py-2">
               <input type="name" id="name" ref="name" class="form-control"  placeholder="Name" v-model="name" :required="required">
            </div>
            <div class="col-12 col-md-6 py-2">
               <input type="email" id="email" ref="email" class="form-control"  placeholder="Email" v-model="email" :required="required">
            </div>
    
            <div class="py-2">
              <input type="text" id="phone" ref="phone" class="form-control"  placeholder="Phone" v-model="phone" :required="required">
            </div>
            <div>
            <textarea id="textarea" ref="textarea" class="form-control" placeholder="Description.." rows="3" max-rows="6" v-model="textarea" :required="required"></textarea>
        </div>
        <input type="hidden" id="business_user_id" ref="business_user_id" :value=bprofile.id>
        <input type="hidden" id="logged_user_id" ref="logged_user_id" :value=loggedUser.id>
      </div>
        <div class="mt-3 py-2">
            <button class="btn btn-success" @click.prevent="getAllData()" type="submit">Submit</button>
            <span class="text-success ms-3">{{ inquiryMessage }}</span>
        </div>
      </form>
     </b-modal>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
          <template v-slot:action="{}">
            <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
          </template>
        </v-snackbar>
    </div>
</template>
<script>
import carousel from "vue-owl-carousel";
import axios from "axios";
export default {
  name: "BusinessView",
  props: {
    id: Number,
    uname: String
  },
  components: { carousel },
  data() {
    return {
      yemd:'https://www.youtube.com/embed/',
      index: null,
      bprofile: [],
      allhotdeal: {},
      showModel1: false,
      rating: '',
      review: '',
      count1: '',
      count2: '',
      count3: '',
      count4: '',
      count5: '',
      dialog: false,
      itemsPerRow:2,
      name:'',
      direction: "left",
      direction1: "bottom",
      fab: false,
      url:'https://bizlx.itechvision.in',
      fling: true,
      hover: true,
      tabs: null,
      transition: "scale-transition",
      panel1: 0,
      panel2: 0,
      disabled: false,
      loggedUser: [],
      sliderimage: [],
      tabsarray: [],
      inquiryMessage:'',
      timeout:'',
      snackbar:false,
      text:'',
      dummyImages: [
        {id:1,src: "https://bizlx.itechvision.in/images/banner.jpg",}
      ],
      ReviewRules: [
        v => !!v || 'Review Description is required.',
    ],
    };
  },
  created(){
      this.loggedUser = this.$store.state.userData;
  },
  mounted() {
    this.businessprofile();
    this.getAllData();
  },
  methods:{
    businessprofile(){
      axios.get(`/businesses/profile/${this.id}`)
          .then((resp) =>{
            
            this.bprofile = resp.data.businessprofile;
            this.allhotdeal = resp.data.allhotdeal;
            // console.log(this.allhotdeal[0].hotdeals_images[0].hotdeal_img_url);
            this.count1 = resp.data.count1;
            this.count2 = resp.data.count2;
            this.count3 = resp.data.count3;
            this.count4 = resp.data.count4;
            this.count5 = resp.data.count5;
            this.tabsarray = resp.data.tabs;
            this.sliderimage = resp.data.coverimages;
          })
    },
    getAllData(){

      var data = {
        name: this.$refs.name.value,
        email: this.$refs.email.value,
        phone: this.$refs.phone.value,
        textarea: this.$refs.textarea.value,
        business_user_id: this.$refs.business_user_id.value,
        logged_user_id: this.$refs.logged_user_id.value,
      };
      axios.post("inquirey", data)
      .then((resp) => {
        if(resp.data.success == true)
          this.inquiryMessage = resp.data.message;
          this.name ="";
          this.email ="";
          this.phone ="";
          this.textarea ="";
          document.getElementById("frm").reset();
      });
    },
    openReviewModal() {
        this.showModel1 = true;
      },
      reviewForm(){
         var reviewData = {
          review_business_id: this.bprofile.id,
          review_user_id: this.loggedUser.id,
          rating: this.rating,
          description: this.review,
        };
        axios.post("add/business/review", reviewData)
        .then((resp) => {
          if(resp.data.success == true)
          this.showModel1 = false;
          this.snackbar = true;
          this.text = resp.data.message;
          this.timeout = 3000;
        });
        this.$refs.ReviewForm.reset();

      }
  },
  
  
  computed: {
    catGroups () {
      return Array.from(Array(Math.ceil(this.bprofile.allhotdeal.length / this.itemsPerRow)).keys())
    },
    
  },
};
</script>
